@import '../../assets/styles/index.scss';

.login-form {
	width: 300px;
	margin: 0 auto;
	padding: 20px;
	border: 1px solid #ccc;
	border-radius: 5px;
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
	text-align: center;
}

.login-form h2 {
	margin-bottom: 20px;
}

.login-form form {
	display: flex;
	flex-direction: column;
}

.login-form label {
	font-weight: bold;
	margin-bottom: 5px;
}

.login-form input {
	padding: 10px;
	margin-bottom: 10px;
	border: 1px solid #ccc;
	border-radius: 3px;
}

.login-form button {
	padding: 10px;
	background-color: #007bff;
	color: white;
	border: none;
	border-radius: 3px;
	cursor: pointer;
}

.login-form button:hover {
	background-color: #0056b3;
}
